.button {
    display: inline-block;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    border-radius: 5px;
    border: none;
    transition: background-color 0.3s ease;
}

.button-default {
    color: #fff;
    background-color: #007bff;
}

.button:hover {
    background-color: #0056b3;
}

.button:active {
    background-color: #004080;
}

.button-large {
    padding: 15px 30px;
    font-size: 20px;
}

.button-small {
    padding: 8px 16px;
    font-size: 14px;
}

.button-icon {
    padding-left: 40px;
    background-repeat: no-repeat;
    background-position: 10px center;
}

.button-container {
    text-align: right;
}
